<template>
  <!-- 评论面板 -->
  <div>
    <div class="panel panel-primary">
      <div class="panel-heading">
        <h3 class="panel-title">发表评论</h3>
      </div>
      <el-form
        label-position="top"
        label-width="80px"
        :model="form"
        ref="form"
        class="panel-body"
        @submit.native.prevent="loginHandler"
      >
        <el-form-item label="评论内容:" :rules="rules.content" prop="content" ref="comment">
          <el-input
            type="textarea"
            placeholder="请写下你对该学院的评论"
            v-model.trim.lazy="form.content"
            :autosize="{ minRows: 2, maxRows: 4 }"
            maxlength="150"
            show-word-limit
            :validate-event="false"
          ></el-input>
        </el-form-item>
        <el-button type="primary" native-type="submit">发表评论</el-button>
      </el-form>
    </div>
    <div class="panel panel-primary">
      <div class="panel-heading">
        <h3 class="panel-title">评论列表</h3>
      </div>
      <!-- 评论列表 -->
      <ul class="list-group">
        <li class="list-group-item" v-for="item in commentList" :key="item.id">
          {{ item.content }}
          <div class="tag-group">
            <el-tag type="warning" effect="dark" size="small"> 评论人：{{ item.username }}</el-tag>
            <el-tag type="primary" effect="dark" size="small"> 评论时间：{{ item.time }}</el-tag>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { getSchoolCmtListAPI, addUserCmtAPI } from '@/api/userCmtAPI.js'
import { content } from '@/components/formValidate'
export default {
  created() {
    this.initCmtList(this.schoolId)
  },
  props: {
    schoolId: {
      type: Number
    }
  },
  data() {
    return {
      form: {
        content: '',
        schoolId: this.schoolId
      },
      commentList: [],
      rules: {
        content
      }
    }
  },
  watch: {
    schoolId: {
      handler() {
        this.initCmtList(this.schoolId)
      }
    }
  },
  methods: {
    async initCmtList() {
      try {
        const { data: result } = await getSchoolCmtListAPI({ school_id: this.schoolId })
        this.commentList = result.data
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    loginHandler() {
      this.addUserComment()
    },
    addUserComment() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          const { data: result } = await addUserCmtAPI({
            school_id: this.form.schoolId,
            content: this.form.content
          })
          if (result.status !== 0) {
            return this.$mynotify.error(result.message)
          }
          // 发表成功
          this.$mynotify.success(result.message)
          this.initCmtList()
          this.form.content = ''
        }
      })
      setTimeout(() => {
        this.$refs.comment.clearValidate()
      }, 2000)
    }
  }
}
</script>

