<template>
  <div class="info-container">
    <!-- 标题 -->
    <div class="info-title">{{ info.name }}</div>
    <!-- 图片 -->
    <div class="info-image">
      <el-carousel :interval="5000" type="card" height="350px" trigger="click" :autoplay="true">
        <el-carousel-item v-for="(item, index) in this.photos" :key="index">
          <el-image v-if="baseUrl" :src="baseUrl + '' + item" fit="contain"></el-image>
          <div v-else class="image-empty">
            <i class="el-icon-picture-outline"></i>
            <span>暂无图片</span>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
    <!-- 内容 -->
    <div class="info-body">
      <el-descriptions class="margin-top" title="属性信息" :column="3" border>
        <template slot="extra">
          <el-button type="primary" size="small" @click="$router.push('/manage/schooledit/' + id)"
            >编辑</el-button
          >
        </template>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-school"></i>
            院校名称
          </template>
          {{ info.name }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-location-outline"></i>
            所在地
          </template>
          {{ info.city }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-s-management"></i>
            教育主管部门
          </template>
          {{ info.department }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-s-data"></i>
            院校类型
          </template>
          {{ info.type }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-medal-1"></i>
            学历层次
          </template>
          {{ info.level }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-tickets"></i>
            院校特性
          </template>
          <el-tag type="success" size="mini" v-if="info.topschool">一流高校</el-tag>
          <el-tag size="mini" v-if="info.topcourse">一流学科</el-tag>
          <el-tag type="warning" size="mini" v-if="info.postgraduate">研究生院</el-tag>
        </el-descriptions-item>
      </el-descriptions>
      <el-descriptions class="margin-top" title="空间信息" :column="3" border>
        <el-descriptions-item>
          <template slot="label"> 经度 </template>
          {{ info.lon }}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label"> 纬度 </template>
          {{ info.lat }}
        </el-descriptions-item>
      </el-descriptions>
    </div>
    <SchoolComment :schoolId="parseInt(id)"></SchoolComment>
  </div>
</template>

<script>
import { getSchoolInfoById } from '@/api/schoolsAPI.js'
import SchoolComment from '@/views/management/SchoolInfo/SchoolComment.vue'
import { baseURL } from '@/myconfig'
export default {
  components: {
    SchoolComment
  },
  props: {
    id: {
      default: 1120,
      type: [Number, String]
    }
  },
  data() {
    return {
      info: {},
      photos: [],
      baseUrl: ''
    }
  },
  watch: {
    id: {
      handler() {
        this.initSchoolInfo(this.id)
      }
    }
  },
  created() {
    this.initSchoolInfo()
  },
  methods: {
    async initSchoolInfo() {
      try {
        const { data: result } = await getSchoolInfoById(this.id)
        if (result.status !== 0) return this.$mynotify.warning(result.message)
        this.info = result.data
        this.initSchoolImage()
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    initSchoolImage() {
      if (this.info.photos) {
        this.photos = this.info.photos
        this.baseUrl = baseURL
      } else {
        this.baseUrl = ''
        this.photos = 6
      }
    }
  }
}
</script>

