.info-title {
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei',
    '微软雅黑', Arial, sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.5;
  margin-bottom: 20px;
  text-align: center;
}
.el-image {
  width: 100%;
  height: 100%;
}
.image-empty {
  width: 100%;
  height: 100%;
  background-color: #f5f7fa;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  span {
    margin-top: 10px;
  }
}
.el-descriptions {
  margin-top: 20px;
}
</style>
