<template>
  <div>
    <el-form label-width="130px" :model="form" ref="form" class="school-container">
      <div class="school-head">
        <h3>{{ this.form.name }}</h3>
      </div>
      <div class="school-body">
        <div class="school-feature">
          <el-form-item label="院校所在地">
            <el-select placeholder="请选择地点" v-model="form.city">
              <el-option
                v-for="(item, index) in formlabel.cities.slice(1)"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <!-- 主管部门也行，但是需要找到所有的值 -->
          <el-form-item label="院校类型">
            <el-select placeholder="请选择地点" v-model="form.type">
              <el-option
                v-for="(item, index) in formlabel.type.slice(1)"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学历层次">
            <el-select placeholder="请选择地点" v-model="form.level">
              <el-option
                v-for="(item, index) in [...formlabel.level.slice(1), '本科/高职(专科)']"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="经度" :rules="rules.lon" prop="lon">
            <el-input v-model.number="form.lon"></el-input>
          </el-form-item>
          <el-form-item label="纬度" :rules="rules.lat" prop="lat">
            <el-input v-model.number="form.lat"></el-input>
          </el-form-item>
        </div>
        <div class="school-public">
          <el-form-item
            v-for="(item, index) in formlabel.public"
            :label="item"
            :key="index"
            label-width="180px"
          >
            <el-switch v-model="form[publicVal[index]]"> </el-switch>
          </el-form-item>
        </div>
        <div class="school-private">
          <el-form-item
            v-for="(item, index) in formlabel.private"
            :label="item"
            :key="index"
            label-width="180px"
          >
            <el-switch v-model="form[privateVal[index]]"> </el-switch>
          </el-form-item>
        </div>
      </div>
      <div class="school-photos">
        <el-form-item label="编辑照片墙">
          <el-upload
            list-type="picture-card"
            ref="uploads"
            :on-remove="handleRemove"
            multiple
            action=""
            name="photos"
            :auto-upload="false"
            :file-list="fileList"
            :http-request="uploadPhotos"
            :on-change="handleChange"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
      </div>
      <div class="school-foot">
        <el-button @click="submitHandler">保存</el-button>
        <el-button @click="cancel">取消</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import {
  deleteSchoolPhotosAPI,
  getSchoolInfoById,
  updateSchoolInfoByIdAPI
} from '@/api/schoolsAPI.js'
import { lon, lat } from '@/components/formValidate'
import formlabel from '@/components/formlabel'
import { diffFormData } from '@/utils/baseutils'
import { baseURL } from '@/myconfig'
export default {
  created() {
    this.initSchoolForm()
  },
  watch: {
    id: {
      handler() {
        if (this.id !== this.form.id) {
          this.initSchoolForm()
        }
      }
    }
  },
  props: ['id'],
  data() {
    return {
      formlabel: formlabel,
      form: {
        name: '',
        id: this.id,
        city: '',
        type: '',
        level: '',
        topschool: false,
        topcourse: false,
        postgraduate: false,
        isprivate: false,
        isindependent: false,
        issinoforeign: false,
        iscowithhmt: false,
        lon: 0,
        lat: 0,
        photos: []
      },
      formClone: {},
      diffFrom: {},
      formObject: {},
      fileList: [],
      deletList: [],
      publicVal: ['topschool', 'topcourse', 'postgraduate'],
      privateVal: ['isprivate', 'isindependent', 'issinoforeign', 'iscowithhmt'],
      rules: {
        lon,
        lat
      }
    }
  },
  methods: {
    // 初始化页面表单的默认数据
    async initSchoolForm() {
      // 需要删除的图片id列表清空
      this.deletList = []
      // 页面浏览的图片列表清空
      this.fileList = []
      try {
        const { data: results } = await getSchoolInfoById(this.id)
        if (results.status !== 0) return this.$mynotify.warning(results.message)
        for (const key in this.form) {
          this.form[key] = results.data[key]
        }
        // 克隆表单原始数据
        this.formClone = { ...this.form }
        // 如果院校有图片。则预览所有图片
        if (this.form.photos) {
          this.form.photos.every((item, index) => {
            return this.fileList.push({
              name: item.substr(item.lastIndexOf('/') + 1),
              url: baseURL + item,
              id: index + 1
            })
          })
        }
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    // 取消已选单选框
    RatioChange(val, e) {
      val === this.form.private ? (this.form.private = '') : (this.form.private = val)
    },
    handleRemove(file, fileList) {
      if (file.status === 'success') {
        this.deletList.push(file.id)
        this.fileList = fileList
      }
    },
    handleChange(file, fileList) {
      this.fileList = fileList
      console.log(file)
    },
    submitHandler() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.diffFrom = diffFormData(this.form, this.formClone)
          if (this.validFormData(this.diffFrom, this.deletList, this.fileList)) {
            this.$myconfirm('确定要保存这些修改吗', { type: 'warning' })
              .then(async () => {
                // 创建表单对象，用于发送类型为formdata类型的表单
                this.createFormData()
                // 提交表单
                this.submitSchoolFormData()
                this.formClone = { ...this.form }
                this.diffFrom = {}
                this.deletList = []
                this.fileList = []
                setTimeout(() => {
                  this.$router.back()
                }, 500)
              })
              .catch()
          }
        }
      })
    },
    createFormData() {
      // 因为上传文件，需要用formdata格式上传，则得创建表单对象
      this.formObject = new FormData()
      // 1.增加id字段
      this.formObject.append('id', this.form.id)
      // 2.填充formData对象
      for (const key in this.diffFrom) {
        this.formObject.append(key, this.diffFrom[key])
      }
      // 3.填充photos字段
      this.$refs.uploads.submit()
    },
    async submitSchoolFormData() {
      // 先删除不想要的图片
      this.deleteSchoolPhotos()
      // 再更新表单以及新上传的图片
      try {
        const { data: result } = await updateSchoolInfoByIdAPI(this.formObject)
        if (result.status === 0) {
          this.$mynotify.success(result.message)
        } else {
          return this.$mynotify.warning(result.message)
        }
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    async deleteSchoolPhotos() {
      if (this.deletList.length > 0) {
        try {
          const { data: result } = await deleteSchoolPhotosAPI(this.id, {
            photos: this.deletList
          })
          if (result.status === 0) {
            this.$mynotify.success(result.message)
          } else {
            return this.$mynotify.warning(result.message)
          }
        } catch (error) {
          this.$mynotify.error(error.message)
        }
      }
    },

    cancel() {
      this.$router.back()
    },
    uploadPhotos(e) {
      this.formObject.append('photos', e.file)
    },
    validFormData(diffForm, deletList, fileList) {
      const isChanged = Object.keys(diffForm).length > 0
      const isExistPhotoId = deletList.length > 0
      const isExistReadyPhoto = fileList.some((item) => {
        return item.status === 'ready'
      })
      return isChanged || isExistPhotoId || isExistReadyPhoto
    }
  },
  beforeRouteLeave(to, from, next) {
    this.diffFrom = diffFormData(this.form, this.formClone)
    if (this.validFormData(this.diffFrom, this.deletList, this.fileList)) {
      // 这里必须延迟执行，要先让浏览器后退，再进行监听处理，否则会闪现
      setTimeout(() => {
        this.$myconfirm('未保存的修改都会丢失，继续吗?', { type: 'warning' })
          .then(() => {
            next()
          })
          .catch(() => {
            next(false)
          })
      }, 200)
    } else {
      next()
    }
  }
}
</script>

<style lang="less" scoped>
.school-container {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  .school-head {
    display: flex;
    justify-content: center;
  }
  .school-body {
    display: flex;
    flex-direction: row;
    > :not(:last-child) {
      margin-right: 15px;
    }
  }
  .school-foot {
    display: flex;
    justify-content: flex-end;
  }
}
</style>
