// import qs  from 'qs';
import { requestUserCmt } from '@/utils/request.js'
import qs from 'qs'
const getSchoolCmtListAPI = (options) => {
  return requestUserCmt.get('/list', {
    params: options
  })
}
const addUserCmtAPI = (data) => {
  return requestUserCmt.post('/add', qs.stringify(data))
}
export { getSchoolCmtListAPI, addUserCmtAPI }
