<template>
  <el-form :model="form" label-width="70px" @submit.native.prevent="loginHandler" ref="form">
    <el-form-item label="用户名" class="line-item" prop="username" :rules="rules.username">
      <el-input v-model="form.username"></el-input>
    </el-form-item>
    <el-form-item label="密码" class="line-item" prop="password" :rules="rules.password">
      <el-input v-model="form.password" show-password></el-input>
    </el-form-item>
    <div class="form-footer line-item">
      <el-button @click="cancleHandler">取 消</el-button>
      <el-button type="primary" native-type="submit">确 定</el-button>
    </div>
  </el-form>
</template>

<script>
import { username, password } from '@/components/formValidate'
import { userLogin } from '@/api/usersAPI.js'
import tokenStore from '@/store/token.js'
import bus from '@/components/evenBus'
export default {
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username,
        password
      }
    }
  },
  methods: {
    loginHandler() {
      if (this.submitLoginForm()) {
        bus.$emit('dialog-visible', false)
      }
    },
    // 提交登陆表单
    submitLoginForm() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          try {
            const { data: result } = await userLogin(this.form)
            if (result.status === 0) {
              this.$mynotify.success(result.message)
              // 设置token
              tokenStore.setTokenAction(result.token)
              // 并初始化用户信息
              bus.$emit('userinfo-change')
              bus.$emit('dialog-visible', false)
              location.reload()
            } else {
              this.$mynotify.warning(result.message)
            }
          } catch (error) {
            this.$mynotify.error(error.message)
          }
          return true
        } else {
          return false
        }
      })
    },
    cancleHandler() {
      bus.$emit('dialog-visible', false)
    }
  }
}
</script>

<style lang="less" scoped>
.line-item {
  margin-right: 20px;
}
.form-footer {
  text-align: right;
}
</style>
