import axios from 'axios'
import { baseURL } from '@/myconfig.js'
import tokenStore from '@/store/token.js'
const requestSchool = axios.create({
  baseURL: baseURL + '/schools'
})
const requestUser = axios.create({
  baseURL: baseURL + '/users'
})
const requestUserInfo = axios.create({
  baseURL: baseURL + '/my'
})
const requestUserCmt = axios.create({
  baseURL: baseURL + '/cmt'
})
// 拦截所有/schools的请求
requestSchool.interceptors.request.use((config) => {
  if (tokenStore.state.token) {
    config.headers.Authorization = tokenStore.state.token
  }
  return config
})
// 拦截所有/my的请求
requestUserInfo.interceptors.request.use((config) => {
  if (tokenStore.state.token) {
    config.headers.Authorization = tokenStore.state.token
  }
  return config
})
// 拦截所以/cmt请求
requestUserCmt.interceptors.request.use((config) => {
  if (tokenStore.state.token) {
    config.headers.Authorization = tokenStore.state.token
  }
  return config
})
export { requestSchool, requestUser, requestUserInfo, requestUserCmt }
