import Vue from 'vue'
import VueRouter from 'vue-router'
import MyHome from '@/views/home/MyHome.vue'
import MyManage from '@/views/management/MyManage.vue'
import SchoolList from '@/views/management/SchoolList/SchoolList.vue'
import SchoolInfo from '@/views/management/SchoolInfo/SchoolInfo.vue'
import SchoolEdit from '@/views/management/SchoolEdit/SchoolEdit.vue'
import tokenStore from '@/store/token.js'
Vue.use(VueRouter)

// 先执行路由，再执行渲染DOM结构
if (localStorage.getItem('token')) {
  tokenStore.state.token = localStorage.getItem('token')
}
const routes = [
  { path: '/', redirect: '/home' },
  { path: '/home', component: MyHome },
  {
    path: '/manage',
    component: MyManage,
    redirect: '/manage/schoollist',
    children: [
      { path: 'schoolinfo', redirect: '/manage/schoolinfo/1120' },
      { path: 'schoollist', component: SchoolList },
      // 跳转时直接将动态参数存入props
      { path: 'schoolinfo/:id', component: SchoolInfo, props: true },
      { path: 'schooledit/:id', component: SchoolEdit, props: true }
    ]
  }
]

const router = new VueRouter({
  routes
})
// router.beforeEach((to, from, next) => {})
export default router
