<template>
  <el-main>
    <div id="map">
      <QueryBox @show-uniPoints="showHandler" @click-item="clickItemHandler"></QueryBox>
      <PopOver ref="mypopup" :info="info"></PopOver>
    </div>
  </el-main>
</template>

<script>
// 地图相关
import Map from 'ol/Map'
import View from 'ol/View'
import { defaults } from 'ol/control'
import * as olProj from 'ol/proj'
import GeoJSON from 'ol/format/GeoJSON'
import VectorSource from 'ol/source/Vector'
import VectorLayer from 'ol/layer/Vector'
import Style from 'ol/style/Style'
import Fill from 'ol/style/Fill'
import Stroke from 'ol/style/Stroke'
import { getOnlineLayer } from '@/utils/layertools.js'
import Feature from 'ol/Feature'
import { Point } from 'ol/geom'
import { fromLonLat } from 'ol/proj'
import QueryBox from '@/components/map/QueryBox/QueryBox.vue'
import CircleStyle from 'ol/style/Circle'
import Text from 'ol/style/Text'
import Heatmap from 'ol/layer/Heatmap'
import bus from '../evenBus'
import Select from 'ol/interaction/Select'
// import { getDistance } from 'ol/sphere'

// import { upAndDown } from 'ol/easing'

// DOM相关
import PopOver from '@/components/map/PopOver/PopOver.vue'
import Overlay from 'ol/Overlay'

export default {
  created() {
    bus.$on('mapping-change', this.mappingHandler)
  },
  mounted() {
    this.initMap()
    this.initInteraction()
    this.initOverlay()
  },
  components: {
    QueryBox,
    PopOver
  },
  props: {
    basemap: {
      default: '',
      type: String
    }
  },

  data() {
    return {
      Map: null,
      view: null,
      uniSource: null,
      uniLayer: null,
      popup: null,
      zoomlevel: 4,
      info: {},
      heatmap: null
      // oldCoord: olProj.transform([110, 39], 'EPSG:4326', 'EPSG:3857'),
      // newCoord: olProj.transform([110, 39], 'EPSG:4326', 'EPSG:3857')
    }
  },
  // 监听底图的变化，变换图层
  watch: {
    basemap: {
      handler(newVal) {
        this.switchBaseMap(newVal)
      }
    }
  },
  methods: {
    // 初始化地图
    initMap() {
      this.view = new View({
        zoom: this.zoomlevel,
        center: olProj.transform([110, 39], 'EPSG:4326', 'EPSG:3857')
      })
      this.Map = new Map({
        controls: defaults({
          zoom: false,
          attribution: false
        }),
        target: 'map',
        layers: [getOnlineLayer(this.basemap), this.getChinaBoundary(), this.createUniLayer()],
        view: this.view
      })
      window.map = this.Map
    },
    // 初始化select交互
    initInteraction() {
      const select = new Select({
        condition: function (e) {
          return e.type === 'singleclick'
        },
        layers: [this.uniLayer],
        // 🙅不想改变样式需要设置为null
        style: null
      })
      this.Map.addInteraction(select)
      // select和deselect都会触发该事件
      select.on('select', (e) => {
        // select选中样式，是不会改变原先的样式，只会在原先样式的基础上改变样式，取消选中，则会还原成原来的样式
        // 设置样式
        const feature = e.selected[0]
        if (!feature) {
          this.popup.setPosition(undefined)
          this.clearStyle(this.uniSource.getFeatures())
          return
        }
        // 高亮要素
        this.highLightPoint(feature)
      })
    },
    // 高亮所选点
    highLightPoint(feature, delay) {
      // 1.所有点要素还原默认属性
      this.clearStyle(this.uniSource.getFeatures())
      // 2.获取要素上的属性信息
      this.info = feature.get('attr')
      // 3.设置高亮要素的样式
      feature.setStyle(this.selectedStyle(this.info.name))
      // 4.获取横轴墨卡托坐标，获取几何坐标，不要获取点击坐标
      const coord = feature.getGeometry().getCoordinates()
      // 显示弹出框
      if (delay) {
        setTimeout(() => {
          this.popup.setPosition(coord)
        }, delay)
      } else {
        this.popup.setPosition(coord)
      }
    },
    initOverlay() {
      this.popup = new Overlay({
        element: this.$refs.mypopup.$el,
        autoPan: {
          // true已经被弃用，现在直接写对象
          animation: {
            duration: 250
          },
          margin: 30
        }
      })
      this.Map.addOverlay(this.popup)
    },
    // 底图图层
    switchBaseMap(val) {
      const layers = this.Map.getLayers()
      if (layers.item(0).get('name') === 'BaseMap') {
        layers.setAt(0, getOnlineLayer(val))
        // 底图初始化完毕
        this.$emit('map-created', this.Map)
      } else {
        layers.insertAt(0, getOnlineLayer(val))
      }
    },
    // 中国边界图层
    getChinaBoundary() {
      const vectorSource = new VectorSource({
        // 从json读取要素的第三种方法，最佳，这里有url属性，自动转换投影
        url: './geojson/Chinaboundaryline.geojson', // 这个url方式的要素，在解析过程中会自动的将要素的投影转换成视图的投影
        format: new GeoJSON() // 对geojson读取和写数据的一个要素格式类
      })

      const vector = new VectorLayer({
        name: 'ChinaBoundary',
        source: vectorSource,
        style: new Style({
          fill: new Fill({
            color: '#4e98f444'
          }),
          stroke: new Stroke({
            width: 3,
            color: [71, 137, 227, 1]
          })
        })
      })
      return vector
    },
    // 创建大学图层
    createUniLayer() {
      const source = new VectorSource({
        features: []
      })
      const layer = new VectorLayer({
        name: 'universities',
        source: source
      })
      this.uniSource = source
      window.uniSource = source
      this.uniLayer = layer
      window.uniLayer = layer
      return layer
    },
    // 创建大学点要素
    createUniFeatures(data) {
      // 清空要素
      this.uniSource.clear()
      const features = []
      for (let i = 0; i < data.length; i++) {
        const coord = fromLonLat([data[i].lon, data[i].lat])
        const feature = new Feature({
          geometry: new Point(coord),
          attr: data[i]
        })
        feature.setId(data[i].id)
        features.push(feature)
      }
      // 添加要素
      this.uniSource.addFeatures(features)
    },
    // 得到热力图图层
    getHeatMap() {
      const heatmap = new Heatmap({
        name: 'HeatMap',
        source: this.uniSource,
        blur: 18,
        radius: 6,
        opacity: 0.75
      })
      return heatmap
    },

    // 处理show-uniPoints事件
    showHandler(e) {
      this.clearStyle(this.uniSource.getFeatures())
      this.createUniFeatures(e)
    },
    // 缩放到图层
    zoomToLayer(id, lon, lat) {
      const view = this.view
      const coord = olProj.transform([lon, lat], 'EPSG:4326', 'EPSG:3857')
      // 新坐标
      // this.newCoord = coord
      // 同时缩放和平移
      view.animate(
        {
          zoom: 4,
          duration: 500
        },
        {
          zoom: 11,
          center: coord
        }
      )
      // console.log(getDistance(this.oldCoord, this.newCoord))
      // 旧坐标
      // this.oldCoord = coord
      const feature = this.uniSource.getFeatureById(id)
      this.highLightPoint(feature, 1000)
      // 弹出框
    },
    // 处理click-item事件
    clickItemHandler(e) {
      this.zoomToLayer(e.id, e.lon, e.lat)
    },
    // 处理mapping-handler事件
    mappingHandler(e) {
      if (e === 'heatmap') {
        this.heatmap = this.getHeatMap()
        this.Map.getLayers().push(this.heatmap)
      } else {
        this.Map.getLayers().remove(this.heatmap)
        this.heatmap = null
      }
    },
    // 选中样式
    selectedStyle(name) {
      return new Style({
        image: new CircleStyle({
          radius: 8,
          fill: new Fill({
            color: '#3A82F7'
          }),
          stroke: new Stroke({
            color: '#C7C7CB',
            width: 2
          })
        }),
        text: new Text({
          text: name,
          fill: new Fill({
            color: '#000'
          }),
          textAlign: 'left',
          scale: 1.5,
          offsetX: '10',
          offsetY: 10,
          stroke: new Stroke({
            color: '#fff',
            width: 1
          })
        })
      })
    },
    // 清空样式
    clearStyle(features) {
      features.every((item) => {
        if (item.getStyle()) {
          item.setStyle(null)
        }
        return true
      })
      this.popup.setPosition(undefined)
    }
  }
}
</script>

<style lang="less" scoped>
.el-main {
  padding: 0;
  margin: 0;
}
#map {
  height: 100%;
  width: 100%;
  position: relative;
}
</style>
