const stroe = {
  state: {
    user_pic: '',
    username: '',
    email: '',
    school_id: undefined
  },
  setUserInfoAction(obj) {
    for (const key in this.state) {
      this.state[key] = obj[key]
    }
  },
  setUserPicAction(val) {
    this.state.user_pic = val
  },
  clearUserInfoAction() {
    for (const key in this.state) {
      this.state[key] = ''
    }
  }
}
export default stroe
