const store = {
  state: {
    token: ''
  },
  setTokenAction(val) {
    this.state.token = val
    localStorage.setItem('token', val)
  },
  clearTokenAction() {
    this.state.token = ''
    localStorage.removeItem('token')
  }
}
export default store
