import qs from 'qs'
import { requestUser } from '@/utils/request.js'

const userLogin = (data) => {
  // 默认就是x/www-form-urlencoded格式
  return requestUser.post('/login', qs.stringify(data))
}
const userSignUpAPI = (data) => {
  console.log(data)
  return requestUser.post('/signup', qs.stringify(data))
}

export { userLogin, userSignUpAPI }
