import TileLayer from 'ol/layer/Tile'
import XYZ from 'ol/source/XYZ'
/**
 * @description: return an online basemap layer
 * @param {String} lyr one of 'ArcGIS','autonavi','BaseMap'
 * @return {Layer} return ol.Layer.Layer
 */
function getOnlineLayer(lyr) {
  const token = '208b5256e3d10046ff51e4eaabbb9bb8'
  let url
  if (lyr === 'ArcGIS') {
    url =
      'http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetPurplishBlue/MapServer/tile/{z}/{y}/{x}'
  } else if (lyr === 'autonavi') {
    url = 'http://wprd0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&style=7&x={x}&y={y}&z={z}' // 高德地图
  } else {
    url = 'http://t{1-7}.tianditu.com/DataServer?T=' + lyr + '&x={x}&y={y}&l={z}&tk=' + token // 天地图得有key才行
  }

  const layer = new TileLayer({
    name: 'BaseMap',
    source: new XYZ({
      // 这种数据源有url属性，加载网络上的资源
      url: url
    })
  })
  return layer
}
export { getOnlineLayer }
