<template>
  <el-dropdown trigger="click">
    <el-tooltip
      :content="userinfo.username ? userinfo.username : '请登录'"
      placement="bottom"
      effect="light"
    >
      <el-avatar
        shape="square"
        size="large"
        :src="userinfo.user_pic"
        :icon="userinfo.username ? '' : 'el-icon-user-solid'"
        fit="cover"
        >{{ userinfo.user_pic ? '' : userinfo.username }}</el-avatar
      >
    </el-tooltip>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-if="!userinfo.username" @click.native="dialogFormVisible = true"
        >登陆</el-dropdown-item
      >
      <el-dropdown-item v-if="userinfo.username" @click.native="dialogUserInfoVisible = true"
        >个人中心</el-dropdown-item
      >
      <el-dropdown-item v-if="userinfo.username" @click.native="logoutHandler"
        >退出</el-dropdown-item
      >
    </el-dropdown-menu>
    <el-dialog :visible.sync="dialogFormVisible" center width="30%" destroy-on-close>
      <LoginDialog></LoginDialog>
    </el-dialog>
    <el-dialog
      title="个人中心"
      :visible.sync="dialogUserInfoVisible"
      center
      width="50%"
      destroy-on-close
    >
      <!-- 关闭这个对话框，就会重启一次 -->
      <UserInfo></UserInfo>
    </el-dialog>
  </el-dropdown>
</template>

<script>
import bus from '@/components/evenBus'
import LoginDialog from '@/components/user/LoginDialog.vue'
import UserInfo from '@/components/user/UserInfoCom.vue'
import { getUserInfoAPI } from '@/api/userInfoAPI.js'
import { getAllSchoolsAPI } from '@/api/schoolsAPI'
import userStore from '@/store/userinfo.js'
import tokenStore from '@/store/token.js'
import schoolNamesStore from '@/store/schoolNameInfo'
export default {
  created() {
    bus.$on('dialog-visible', this.dialogVisibleHandler)
    bus.$on('userinfo-change', this.infoChangeHandler)
    this.initUserInfo()
    this.initAllSchoolMainInfo()
  },
  components: {
    LoginDialog,
    UserInfo
  },
  data() {
    return {
      dialogFormVisible: false,
      dialogUserInfoVisible: false,
      userinfo: userStore.state,
      token: tokenStore.state,
      options: schoolNamesStore.state
    }
  },
  methods: {
    dialogVisibleHandler(e) {
      this.dialogFormVisible = false
    },
    infoChangeHandler() {
      this.initUserInfo()
    },
    logoutHandler() {
      this.$myconfirm('确定要退出吗?', { type: 'warning' })
        .then(() => {
          tokenStore.clearTokenAction()
          userStore.clearUserInfoAction()
          console.log(this.$route)
          if (!this.$route.path !== '/home') {
            this.$router.push('/home')
          }
          location.reload()
        })
        .catch(() => {})
    },
    // 组件的方法一定晚于路由执行的，所以此时storetoken里一定有token
    async initUserInfo() {
      // token存在，则直接登陆,实现免登陆
      if (tokenStore.state.token) {
        try {
          const { data: result } = await getUserInfoAPI()
          // token失效的错误
          if (result.status !== 0) {
            return this.$mynotify.warning(result.message)
          }
          // 获取token所指示的用户信息，存入store
          userStore.setUserInfoAction(result.data)
        } catch (error) {
          this.$mynotify.error(error.message)
        }
      }
    },
    async initAllSchoolMainInfo() {
      try {
        const { data: result } = await getAllSchoolsAPI()
        if (result.status !== 0) return this.$mynotify.warning(result.message)
        schoolNamesStore.setSchoolNameInfoAction(result.data)
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-dropdown {
  line-height: 1;
}
/deep/ .el-dialog {
  border-radius: 5px;
}
.el-avatar {
  cursor: pointer;
}
.el-avatar /deep/ img {
  width: 100%;
}
</style>
