<template>
  <div class="pager-container">
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :page-size="pagerinfo.limit"
      :current-page="this.pagerinfo.offset / 20 + 1"
      @current-change="changehandler"
    >
    </el-pagination>
  </div>
</template>

<script>
import bus from '@/components/evenBus'
export default {
  props: {
    pagerinfo: {
      type: Object,
      default: function () {
        return {}
      }
    },
    total: {
      type: Number,
      default: 2756
    }
  },
  methods: {
    changehandler(index) {
      const offset = (index - 1) * this.pagerinfo.limit
      bus.$emit('page-change', offset)
    }
  }
}
</script>

<style lang="less" scoped>
.pager-container {
  float: right;
}
</style>
