Vue.prototype.$confirm = MessageBox.confirm
Vue.prototype.$notify = Notification

// 封装消息
Vue.prototype.$mynotify = {
  success: (msg) => {
    return Notification.success({
      title: '成功',
      message: msg
    })
  },
  warning: (msg) => {
    return Notification.warning({
      title: '警告',
      message: msg
    })
  },
  error: (msg) => {
    return Notification.error({
      title: '错误',
      message: msg
    })
  }
}
// 封装弹出框
/**
 *
 * @param {String} msg message in confirm
 * @param {Object} option more options
 * @returns {Promise<MessageBoxData>}
 */
Vue.prototype.$myconfirm = (msg, option) => {
  return MessageBox.confirm(msg, '提示', option)
}
/**
 *
 * @param {String} msg message in alert
 * @param {Object} option more options
 * @returns {Promise<MessageBoxData>}
 */
Vue.prototype.$myalert = (msg) => {
  return MessageBox.alert(msg, '提示')
}
/**
 *
 * @param {String} msg message in prompt
 * @param {Object} option more options
 * @returns {Promise<MessageBoxData>}
 */
Vue.prototype.$myprompt = (msg) => {
  return MessageBox.prompt(msg, '提示')
}

// --------注册elementui----------
Vue.config.productionTip = false

new Vue({
  router,
  render: (h) => h(App)
}).$mount('#app')
