<template>
  <el-select v-model="selected.mapping" placeholder="请选择制图方式" @change="mappingChange">
    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    </el-option>
  </el-select>
</template>

<script>
import bus from '@/components/evenBus'
export default {
  data() {
    return {
      selected: {
        mapping: ''
      },
      options: [
        {
          value: 'heatmap',
          label: '热力图'
        },
        {
          value: undefined,
          label: '无'
        }
      ]
    }
  },
  methods: {
    mappingChange() {
      bus.$emit('mapping-change', this.selected.mapping)
    }
  }
}
</script>

