<template>
  <div>
    <div class="form-control">
      <el-switch
        v-model="saved"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text="保存"
        inactive-text="编辑"
        @change="editChangeHandler"
        >>
      </el-switch>
    </div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="头像">
        <el-upload
          class="avatar-uploader"
          action=""
          :disabled="saved"
          :auto-upload="false"
          :show-file-list="false"
          :on-change="handleAvatarChange"
        >
          <el-image
            v-if="form.user_pic"
            :src="form.user_pic"
            class="avatar"
            fit="cover"
            :style="{ cursor: saved ? 'not-allowed' : 'pointer' }"
          ></el-image>
          <i
            v-else
            class="el-icon-plus avatar-uploader-icon"
            :style="{ cursor: saved ? 'not-allowed' : 'pointer' }"
          ></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="用户名"
        ><el-input v-model="form.username" :disabled="saved"></el-input
      ></el-form-item>
      <el-form-item label="邮箱"
        ><el-input v-model="form.email" :disabled="saved"></el-input
      ></el-form-item>
      <el-form-item label="我的大学" class="line-item">
        <el-select
          v-model="form.school_id"
          filterable
          placeholder="请选择你的学校"
          :disabled="saved"
        >
          <el-option
            v-for="item in options.schoolNameInfo"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { updateUserInfoAPI } from '@/api/userInfoAPI.js'
import userStore from '@/store/userinfo.js'
import schoolNamesStore from '@/store/schoolNameInfo'
import bus from '@/components/evenBus.js'
import { fileToBase64, diffFormData } from '@/utils/baseutils.js'
export default {
  created() {
    // 一旦打开用户信息，就考拷贝原始数据
    this.formClone = { ...this.form }
  },
  data() {
    return {
      form: userStore.state,
      formClone: {},
      diffForm: {},
      saved: true,
      options: schoolNamesStore.state
    }
  },
  methods: {
    // switch变化时的处理
    editChangeHandler() {
      if (this.saved) {
        // 保存发送变化的表单数据
        this.diffForm = diffFormData(this.form, this.formClone)
        if (Object.keys(this.diffForm).length > 0) {
          this.$myconfirm('确定要保存吗?', { type: 'warning' })
            .then(async () => {
              // 提交变化的变动数据
              try {
                const msg = await this.submitUserInfoForm()
                this.$mynotify.success(msg)
                // 更新完个人信息后，等待promise状态改变，然后再次获取最新个人信息
                bus.$emit('userinfo-change')
                // 再克隆一遍新数据
                this.formClone = { ...this.form }
              } catch (err) {
                // 遇到错误，无法保存，switch要回去
                this.$mynotify.error(err.message)
                this.saved = false
              }
            })
            .catch(() => {
              this.saved = false
            })
        }
      }
    },
    async submitUserInfoForm() {
      try {
        const { data: result } = await updateUserInfoAPI(this.diffForm)
        if (result.status !== 0) {
          // 保存失败
          return Promise.reject(new Error(result.message))
        }
        return Promise.resolve(result.message)
        // 异步请求
        // 更新成功后,再请求一次最新数据,并把diffrom清空
        // 异步请求，再次请求数据最新数据时
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    // 更换头像的处理函数
    async handleAvatarChange(file, filelist) {
      if (this.beforeAvatarUpload(file.raw)) {
        try {
          const result = await fileToBase64(file.raw)
          userStore.setUserPicAction(result)
        } catch (error) {
          this.$mynotify.error(error.message)
        }
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$mynotify.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        setTimeout(() => {
          this.$mynotify.error('上传头像图片大小不能超过 2MB!')
        }, 0)
      }

      return isJPG && isLt2M
    }
  }
}
</script>
