<template>
  <el-form :model="form" label-width="80px" @submit.native.prevent="signUpHandler" ref="form">
    <el-form-item label="头像">
      <el-upload
        class="avatar-uploader"
        action=""
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleAvatarChange"
        ref="upload"
      >
        <el-image v-if="form.user_pic" :src="form.user_pic" class="avatar" fit="cover"></el-image>
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </el-form-item>
    <el-form-item label="用户名" class="line-item" prop="username" :rules="rules.username">
      <el-input v-model="form.username"></el-input>
    </el-form-item>
    <el-form-item label="密码" class="line-item" prop="password" :rules="rules.password">
      <el-input v-model="form.password" show-password></el-input>
    </el-form-item>
    <el-form-item label="我的大学" class="line-item" prop="school_id" :rules="rules.school_id">
      <el-select v-model="form.school_id" filterable placeholder="请选择你的学校">
        <el-option
          v-for="item in options.schoolNameInfo"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        >
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="邮箱" class="line-item" prop="email" :rules="rules.email">
      <el-input v-model="form.email"></el-input>
    </el-form-item>
    <div class="form-footer line-item">
      <el-button @click="cancleHandler">取 消</el-button>
      <el-button type="primary" native-type="submit">确 定</el-button>
    </div>
  </el-form>
</template>

<script>
import { username, password, email, schoolId } from '@/components/formValidate'
import bus from '@/components/evenBus.js'
import { clearEmptyValue, fileToBase64 } from '@/utils/baseutils.js'
import { userSignUpAPI } from '@/api/usersAPI'
import schoolNamesStore from '@/store/schoolNameInfo'
export default {
  created() {
    this.initAllSchoolMainInfo()
  },
  data() {
    return {
      form: {
        user_pic: '',
        username: '',
        password: '',
        email: '',
        school_id: 1120
      },
      rules: {
        username,
        password,
        email,
        school_id: schoolId
      },
      options: schoolNamesStore.state
    }
  },
  methods: {
    // 更换头像的处理函数
    async handleAvatarChange(file, filelist) {
      if (this.beforeAvatarUpload(file.raw)) {
        try {
          const result = await fileToBase64(file.raw)
          this.form.user_pic = result
        } catch (error) {
          this.$mynotify.error(error.message)
        }
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$mynotify.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        setTimeout(() => {
          this.$mynotify.error('上传头像图片大小不能超过 2MB!')
        }, 0)
      }

      return isJPG && isLt2M
    },
    submitSignupForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$myconfirm('确定要注册吗?', { type: 'warning' })
            .then(async () => {
              try {
                const { data: result } = await userSignUpAPI(clearEmptyValue(this.form))
                if (result.status === 0) {
                  this.$mynotify.success(result.message)
                  this.$refs.form.resetFields()
                  this.form.user_pic = ''
                } else {
                  this.$mynotify.warning(result.message)
                }
              } catch (error) {
                this.$mynotify.error(error.message)
              }
              return true
            })
            .catch(() => {})
        } else {
          return false
        }
      })
    },
    cancleHandler() {
      bus.$emit('dialog-visible', false)
    },
    signUpHandler() {
      this.submitSignupForm()
    }
  }
}
</script>

