<template>
  <!-- 获取的院校信息,limit,offset,total,都在此组件中 -->
  <div class="mian-container">
    <!-- 父组件之所以需要拿到schoollist，是要发送给SchoolTable进行渲染 -->
    <!-- 父组件之所以需要拿到pagerinfo，是要发送给SchoolPager确定页码 -->
    <QueryForm @data-change="getSchoolInfo"></QueryForm>
    <SchoolTable :schoolList="schoolList"></SchoolTable>
    <SchoolPager :pagerinfo="pagerinfo" :total="total"></SchoolPager>
  </div>
</template>

<script>
import QueryForm from '@/components/schools/QueryForm.vue'
import SchoolTable from '@/components/schools/SchoolTable.vue'
import SchoolPager from '@/components/schools/SchoolPager.vue'
export default {
  name: 'SchoolList',
  data() {
    return {
      schoolList: [],
      pagerinfo: {},
      total: 2756
    }
  },
  components: {
    QueryForm,
    SchoolTable,
    SchoolPager
  },
  methods: {
    // 拿到数据后，准备发给schooltable子组件
    getSchoolInfo(e) {
      this.schoolList = e.data ? e.data : this.schoolList
      this.pagerinfo = e.pagerinfo ? e.pagerinfo : this.pagerinfo
      this.total = e.total ? e.total : this.total
    }
  }
}
</script>

<style lang="less" scoped>
.mian-container {
  overflow: inherit;
}
</style>
