<template>
  <el-header>
    <el-row type="flex" class="row-bg">
      <el-col :span="6">
        <!-- 点按功能 -->
        <el-button-group>
          <el-button @click="zoomFull">全国</el-button>
          <el-button @click="zoomIn">放大</el-button>
          <el-button @click="zoomOut">缩小</el-button>
        </el-button-group>
      </el-col>
      <el-col :span="5">
        <!-- 选择功能 -->
        <!-- 切换地图 -->
        <el-select v-model="selected.basemap" placeholder="请选择底图" @change="onMapChange">
          <el-option
            v-for="item in basemapArr"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="5">
        <!-- 制图 -->
        <MappingTools></MappingTools>
      </el-col>
    </el-row>
  </el-header>
</template>

<script>
import * as olProj from 'ol/proj'
import MappingTools from '@/components/map/ToolBoxs/MappingTools.vue'
export default {
  created() {
    this.onMapChange()
  },
  components: {
    MappingTools
  },
  data() {
    return {
      basemapArr: [
        {
          value: 'ArcGIS',
          label: 'ArcGIS'
        },
        {
          value: 'vec_w',
          label: '天地图'
        },
        {
          value: 'autonavi',
          label: '高德地图'
        }
      ],
      selected: {
        // 默认图层
        basemap: 'vec_w'
      }
    }
  },
  methods: {
    // 功能简单，就不使用组件间传递数据
    zoomIn() {
      const currentZoom = window.map.getView().getZoom()
      window.map.getView().setZoom(currentZoom + 1)
    },
    zoomOut() {
      const currentZoom = window.map.getView().getZoom()
      window.map.getView().setZoom(currentZoom - 1)
    },
    zoomFull() {
      const view = window.map.getView()
      view.setZoom(4)
      view.setCenter(olProj.transform([110, 39], 'EPSG:4326', 'EPSG:3857'))
    },
    onMapChange() {
      this.$emit('basemap-change', this.selected.basemap)
    }
  }
}
</script>

