<template>
  <!-- 发送ajax请求获取数据都在此组件中进行, -->
  <div class="from-container">
    <el-form label-width="100px" size="medium" @submit.native.prevent="onSubmit">
      <el-form-item label-width="20px" size="large">
        <el-input
          placeholder="请输入院校名称"
          prefix-icon="el-icon-search"
          class="form-search"
          v-model="form.name"
        >
        </el-input>
        <el-button type="primary" class="search-btn" native-type="submit">查询</el-button>
      </el-form-item>
      <el-form-item label="院校所在地">
        <el-radio-group v-model="form.city" @change="onSubmit">
          <el-radio-button
            v-for="(item, index) in formlabel.cities"
            :key="index"
            :label="item"
          ></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="主管部门类型">
        <el-radio-group v-model="form.department" @change="onSubmit">
          <el-radio-button
            v-for="(item, index) in formlabel.department"
            :key="index"
            :label="item"
          ></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="院校类型">
        <el-radio-group v-model="form.type" @change="onSubmit">
          <el-radio-button
            v-for="(item, index) in formlabel.type"
            :key="index"
            :label="item"
          ></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="学历层次">
        <el-radio-group v-model="form.level" @change="onSubmit">
          <el-radio-button
            v-for="(item, index) in formlabel.level"
            :key="index"
            :label="item"
          ></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="院校特性">
        <el-checkbox-group class="box-group" v-model="form.public" @change="onSubmit">
          <el-checkbox
            class="characteristics"
            v-for="(item, index) in formlabel.public"
            :key="index"
            :label="item"
            name="public"
          ></el-checkbox>
        </el-checkbox-group>
        <el-radio-group class="box-group" v-model="form.private">
          <el-radio
            class="characteristics"
            v-for="(item, index) in formlabel.private"
            :key="index"
            :label="item"
            @click.native.stop.prevent="RatioChange(item, $event)"
          ></el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import formlabel from '@/components/formlabel.js'
import { getSchoolsCountAPI, getSchoolsInfoAPI } from '@/api/schoolsAPI'
import bus from '@/components/evenBus'
export default {
  activated() {
    this.initNextPageList()
  },
  created() {
    // 监听页码的变化
    bus.$on('page-change', this.pageChangeHandler)
    this.initSchoolCount()
  },
  data() {
    return {
      formlabel: formlabel,
      form: {
        name: '',
        city: '全部',
        department: '全部',
        type: '全部',
        level: '全部',
        public: [],
        private: '',
        limit: 20,
        offset: 0
      }
    }
  },
  methods: {
    // 取消已选单选框
    RatioChange(val, e) {
      val === this.form.private ? (this.form.private = '') : (this.form.private = val)
      this.initSchoolCount()
      this.initNextPageList()
    },
    // 初始化第一页数据
    // 并且获取总条数，修改页码
    async initSchoolCount() {
      this.form.offset = 0
      try {
        const { data: res1 } = await getSchoolsCountAPI(this.form)
        if (res1.status !== 0) {
          return this.$mynotify.warning(res1.message)
        }
        this.$emit('data-change', {
          total: parseInt(res1.data[0].count)
        })
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    pageChangeHandler(offset) {
      this.form.offset = offset
      this.initNextPageList()
    },
    // 当页码变化是初始化下一页数据
    async initNextPageList() {
      try {
        const { data: res } = await getSchoolsInfoAPI(this.form)
        if (res.status !== 0) return this.$mynotify.warning(res.message)
        this.$emit('data-change', {
          data: res.data,
          pagerinfo: {
            limit: this.form.limit,
            offset: this.form.offset
          }
        })
      } catch (error) {
        this.$mynotify.error(error.message)
      }
    },
    async onSubmit() {
      this.initSchoolCount()
      this.initNextPageList()
    }
  }
}
</script>

<style lang="less" scoped>
.from-container {
  margin-bottom: 15px;
}
.el-form-item:not(:last-child) {
  margin-bottom: 15px;
}
.el-form-item:last-child {
  margin-bottom: 0;
}
.form-search {
  width: 40%;
  margin-right: 20px;
}
.el-radio-button {
  margin-bottom: 5px;
}
.box-group {
  display: inline-block;
}
.el-checkbox-group {
  margin-right: 30px;
}
.characteristics {
  line-height: 1;
}
</style>
