import { requestSchool } from '@/utils/request.js'
/**
 * @description:you have to add async before function
 * @param {Object} Options query string
 * @return {Promise} promise for school info
 */

const getSchoolsInfoAPI = function (Options) {
  return requestSchool.get('/getschools', {
    // 请求参数
    params: Options
  })
}
const getSchoolsCountAPI = function (Options) {
  return requestSchool.get('/getcount', {
    // 请求参数
    params: Options
  })
}
const getSchoolInfoById = function (id) {
  return requestSchool.get(`/${id}`)
}
const updateSchoolInfoByIdAPI = function (data) {
  return requestSchool.post('/edit', data, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}
const deleteSchoolPhotosAPI = function (id, obj) {
  return requestSchool.get(`/delete/${id}`, {
    params: obj
  })
}
const getAllSchoolsAPI = () => {
  return requestSchool.get('/list')
}
export {
  getSchoolsInfoAPI,
  getSchoolsCountAPI,
  getSchoolInfoById,
  updateSchoolInfoByIdAPI,
  deleteSchoolPhotosAPI,
  getAllSchoolsAPI
}
