<template>
  <div class="form-contain">
    <!-- 静止回车发起同步请求而跳转页面 -->
    <el-form
      size="mini"
      label-width="100px"
      label-position="left"
      @submit.native.prevent="onSubmit"
    >
      <div class="basic-search">
        <el-input
          placeholder="请输入院校名称"
          prefix-icon="el-icon-search"
          class="form-search"
          size="medium"
          v-model="form.name"
        >
        </el-input>
        <el-button type="primary" size="medium" class="search-btn" native-type="submit"
          >查询</el-button
        >
      </div>
      <el-collapse class="advanced-search" accordion v-model="currentPanel">
        <el-collapse-item title="更多选项" name="1">
          <el-form-item label="院校所在地">
            <el-select placeholder="请选择地点" v-model="form.city">
              <el-option
                v-for="(item, index) in formlabel.cities"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="主管部门类别">
            <el-select placeholder="请选择地点" v-model="form.department">
              <el-option
                v-for="(item, index) in formlabel.department"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="院校类型">
            <el-select placeholder="请选择地点" v-model="form.type">
              <el-option
                v-for="(item, index) in formlabel.type"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="学历层次">
            <el-select placeholder="请选择地点" v-model="form.level">
              <el-option
                v-for="(item, index) in formlabel.level"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="院校特性">
            <el-checkbox-group v-model="form.public">
              <el-checkbox
                class="characteristics"
                v-for="(item, index) in formlabel.public"
                :key="index"
                :label="item"
                name="public"
              ></el-checkbox>
            </el-checkbox-group>
            <el-radio-group v-model="form.private">
              <el-radio
                class="characteristics"
                v-for="(item, index) in formlabel.private"
                :key="index"
                :label="item"
                @click.native.stop.prevent="RatioChange(item, $event)"
              ></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-collapse-item>
        <el-collapse-item title="查询结果" name="2" :disabled="forbid">
          <ul
            class="infinite-list"
            v-infinite-scroll="load"
            style="overflow: auto"
            infinite-scroll-disabled="disabled"
          >
            <li
              v-for="item in resultsList"
              :key="item.id"
              @click="onClick(item.id, parseFloat(item.lon), parseFloat(item.lat))"
            >
              <ResultItem
                :id="item.id"
                :name="item.name"
                :topschool="item.topschool"
                :topcourse="item.topcourse"
                :postgraduate="item.postgraduate"
                :satisfaction="item.satisfaction"
              ></ResultItem>
            </li>
            <p v-if="loading">加载中...</p>
            <p v-if="noMore">没有更多了</p>
          </ul>
        </el-collapse-item>
      </el-collapse>
    </el-form>
  </div>
</template>

<script>
import { getSchoolsInfoAPI } from '@/api//schoolsAPI'
import formlabel from '@/components/formlabel.js'
import ResultItem from '@/components/map/QueryBox/ResultItem.vue'
export default {
  components: {
    ResultItem
  },
  data() {
    return {
      formlabel: formlabel,
      // 表单数据
      form: {
        name: '',
        city: '全部',
        department: '全部',
        type: '全部',
        level: '全部',
        public: [],
        private: ''
      },
      limit: 10,
      offset: 0,
      // 手风琴数据
      currentPanel: '',
      currentItem: {},
      // 查询结果数据
      loading: false,
      noMore: false,
      resultsList: [],
      forbid: true,
      iscreated: false
    }
  },
  computed: {
    disabled() {
      return this.noMore || this.loading
    }
  },
  methods: {
    // 取消已选单选框
    RatioChange(val, e) {
      val === this.form.private ? (this.form.private = '') : (this.form.private = val)
    },
    // 该功能应该设置防抖
    // 发起ajax请求，完成两件事
    // 1.触发显示大学点事件
    // 2.初始化第一页查询结果
    async onSubmit() {
      // 显示查询结果
      this.forbid = false
      // 获取所有的高校数据
      try {
        const { data: res } = await getSchoolsInfoAPI(this.form)
        if (res.status !== 0) {
          return this.$mynotify.warning(res.message)
        }
        // 触发显示大学点事件
        this.$emit('show-uniPoints', res.data)
        // 分页查询，显示查询结果
        this.initQueryRes()
      } catch (error) {
        this.$mynotify.error(error.message)
      }

      // BUG1:暂时用延迟打开，这样才不会卡顿
      setTimeout(() => {
        this.currentPanel = '2'
      }, 500)
    },

    // 分页请求院校数据
    async initSchoolsInfoByOffset(begin) {
      this.loading = true
      // 查询参数增加偏移量
      const formCopy = {
        ...this.form,
        limit: this.limit,
        offset: this.offset
      }
      const { data: res } = await getSchoolsInfoAPI(formCopy)
      // 第一次查询则覆盖原数据
      if (begin) {
        this.resultsList = res.data
      } else {
        // 下拉显示更多，则追加数据结果
        this.resultsList = [...this.resultsList, ...res.data]
      }
      this.loading = false
      if (res.total === 0) {
        this.noMore = true
      }
    },
    // 初始化第一页数据
    async initQueryRes() {
      this.noMore = false
      this.offset = 0
      this.initSchoolsInfoByOffset(true)
    },
    // 加载下一页
    async load() {
      this.offset += this.limit
      this.initSchoolsInfoByOffset()
    },
    // 点击item
    onClick(id, lon, lat) {
      // 触发click-item事件
      this.currentItem.id = id
      this.currentItem.lon = lon
      this.currentItem.lat = lat
      this.$emit('click-item', {
        id,
        lon,
        lat
      })
    }
  }
}
</script>

