<template>
  <el-aside width="240px" class="tree-container">
    <div class="tree-header">
      <h3>图层控制</h3>
      <p class="el-icon-info">拖拽图层可改变图层的显示顺序</p>
    </div>
    <div class="tree-body">
      <el-tree
        :data="layerNodes"
        node-key="id"
        default-expand-all
        @node-drag-start="handleDragStart"
        @node-drop="handleDrop"
        draggable
        :allow-drop="allowDrop"
        :allow-drag="allowDrag"
        show-checkbox
        check-strictly
        :default-checked-keys="defaultVisibleLayer"
        @check-change="
          (data, checked) => {
            data.visibilityChange(data, checked)
          }
        "
      >
        <span class="custom-tree-node" slot-scope="{ node, data }">
          <span>{{ node.label }}</span>
          <el-slider
            v-if="node.label === 'opacity'"
            v-model="data.val"
            input-size="mini"
            :min="data.min"
            :max="data.max"
            :step="data.step"
            @input="data.opacityChange"
          ></el-slider>
          <el-slider
            v-if="node.label === 'radius size'"
            v-model="data.val"
            input-size="mini"
            :min="data.min"
            :max="data.max"
            :step="data.step"
            @input="data.radiusChange"
          ></el-slider>
          <el-slider
            v-if="node.label === 'blur size'"
            v-model="data.val"
            input-size="mini"
            :min="data.min"
            :max="data.max"
            :step="data.step"
            @input="data.blurChange"
          ></el-slider>
        </span>
      </el-tree>
    </div>
  </el-aside>
</template>

<script>
import LayerGroup from 'ol/layer/Group'
import Heatmap from 'ol/layer/Heatmap'
import { getIndexof, reOrderArr } from '@/utils//baseutils.js'

export default {
  created() {
    // 初始化图层控制
    this.initLayerTree()
    // 为监听图层
    window.map.getLayers().on('change:length', (e) => {
      const layerGroup = new LayerGroup({ layers: e.target })
      this.createAllLayerNodes('layer', layerGroup)
    })
  },
  data() {
    return {
      layerNodes: [],
      layerArray: [],
      indexBefore: 0,
      indexAfter: 0
    }
  },
  computed: {
    defaultVisibleLayer() {
      const arr = []
      // 当layerNodes改变时，该计算属性就会触发
      this.layerNodes.every((item) => {
        if (item.visibility) {
          arr.push(item.id)
        }
        return true
      })
      return arr
    }
  },
  methods: {
    // 拖拽开始的回调
    handleDragStart(node, ev) {
      this.indexBefore = getIndexof(this.layerNodes, node.data)
    },
    // 拖拽成功结束的回调
    handleDrop(draggingNode, dropNode, dropType, ev) {
      this.indexAfter = getIndexof(this.layerNodes, draggingNode.data)
      // 图层数组是从下标大的显示到下标小的
      // 图层控制节点是下标小的显示到下标大大
      this.layerArray = window.map.getLayers().getArray()
      const length = this.layerArray.length
      reOrderArr(this.layerArray, length - 1 - this.indexBefore, length - 1 - this.indexAfter)
      // 重新刷新图层
      this.reRenderLayers()
    },
    allowDrop(draggingNode, dropNode, type) {
      if (dropNode.data.id.indexOf('prop') !== -1) {
        return false
      } else if (type === 'inner') {
        return false
      } else {
        return true
      }
    },
    allowDrag(draggingNode) {
      // ID不为prop点节点可以拖拽
      return draggingNode.data.id.indexOf('prop') === -1
    },
    // 重新渲染地图
    reRenderLayers() {
      // 请求下一次渲染
      window.map.render()
    },
    initLayerTree() {
      this.createAllLayerNodes('layer', window.map.getLayerGroup())
    },
    // 创建所有图层节点
    createAllLayerNodes(id, group) {
      this.layerNodes = []
      group.getLayers().forEach((layer, i, array) => {
        const layerId = id + (array.length - i - 1)
        if (layer instanceof LayerGroup) {
          this.createAllLayerNodes(layerId, layer)
        } else {
          this.createSingleLayerNode(layerId, layer)
        }
      })
    },
    // 创建每一个图层节点
    createSingleLayerNode(layerId, layer) {
      const layerNode = {}
      // 图层节点的标题
      layerNode.id = layerId
      layerNode.label = layer.get('name')
      layerNode.visibility = layer.get('visible')
      layerNode.visibilityChange = (data, checked) => {
        layer.set('visible', checked)
      }
      // 图层节点的控制内容
      layerNode.children = []
      layerNode.children.push({
        id: layerId + ' prop opacity',
        label: 'opacity',
        val: layer.get('opacity'),
        min: 0,
        max: 1,
        step: 0.01,
        opacityChange: (e) => {
          layer.set('opacity', e)
        }
      })
      // 若是热力图，增加两个图层控制内容
      if (layer instanceof Heatmap) {
        layerNode.children.push({
          id: layerId + ' prop radius size',
          label: 'radius size',
          val: layer.get('radius'),
          min: 1,
          max: 50,
          step: 1,
          radiusChange: (e) => {
            layer.set('radius', e)
          }
        })
        layerNode.children.push({
          id: layerId + ' prop blur size',
          label: 'blur size',
          val: layer.get('blur'),
          min: 1,
          max: 50,
          step: 1,
          blurChange: (e) => {
            layer.set('blur', e)
          }
        })
      }
      // 前面插入节点
      this.layerNodes.unshift(layerNode)
    },
    selectHandler(e) {
      console.log(e)
    },
    loadNode(node, resolve) {
      return resolve(this.layerNodes)
    }
  }
}
</script>

