import qs from 'qs'
import { requestUserInfo } from '@/utils/request.js'

const getUserInfoAPI = () => {
  return requestUserInfo.get('/userinfo')
}

const updateUserInfoAPI = (data) => {
  return requestUserInfo.post('/userinfo', qs.stringify(data))
}

export { getUserInfoAPI, updateUserInfoAPI }
